import Head from "next/head";

const Layout = ({ children }) => {
  return (
    <div className="root">
      <Head>
        <title>Recipe Viewer</title>
      </Head>
      <body>
        {children}

        <footer>&copy; {new Date().getFullYear()}</footer>
      </body>
    </div>
  );
};

export default Layout;
